@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700);
.App {
  width: 1440px;
  height: 4616px;
  box-sizing: border-box;
  background-image: url(/react-landing-page-addstract/static/media/bg.91eb39e9.webp);
  background-repeat: no-repeat;
  box-shadow: inset 0px 0px 100px 100px rgba(248, 250, 253, 0.75);
  padding: 0px 140px; }
  .App header {
    display: flex;
    justify-content: space-between;
    align-items: center; }
    .App header nav {
      padding: 27px 0; }
      .App header nav ul {
        list-style-type: none;
        display: flex;
        margin: 0;
        font-size: 16px;
        color: #6b8cae; }
        .App header nav ul li {
          padding: 0 10px;
          cursor: pointer; }
          .App header nav ul li::before {
            content: '';
            display: block;
            width: 0%;
            height: 1px;
            background-color: #6b8cae;
            margin-bottom: 5px;
            -webkit-transition: all ease 0.2s;
            transition: all ease 0.2s; }
          .App header nav ul li:hover {
            text-shadow: 1px 0 0 #6b8cae; }
            .App header nav ul li:hover::before {
              width: 100%; }
  .App section.impress {
    padding-top: 125px;
    margin-right: -40px;
    background: url(/react-landing-page-addstract/static/media/laptop.0f27710f.webp) 100% 0% no-repeat;
    min-height: 668px;
    -webkit-animation: wave 5s infinite;
            animation: wave 5s infinite;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out; }
    .App section.impress h1 {
      font-size: 48px; }
    .App section.impress .button {
      width: 211px;
      height: 46px;
      line-height: 46px;
      text-align: center;
      margin-top: 68px;
      cursor: pointer;
      -webkit-user-select: none;
         -moz-user-select: none;
          -ms-user-select: none;
              user-select: none;
      color: #f8fafd;
      border-radius: 23px;
      box-shadow: 1px 1px 2px #0000004a;
      background: -webkit-linear-gradient(45deg, #00d4ff 0%, #1a83dd 33%, #1a83dd 66%, #00d4ff 100%);
      background: linear-gradient(45deg, #00d4ff 0%, #1a83dd 33%, #1a83dd 66%, #00d4ff 100%);
      background-size: 200%, 200%;
      background-position: 0px 0px;
      -webkit-transition: all 0.2s ease;
      transition: all 0.2s ease; }
      .App section.impress .button:hover {
        background-position: -200px 0px; }

@-webkit-keyframes wave {
  0%, 20%, 50%, 80%, 100% {
    background-position: 100% 0px; }
  40% {
    background-position: 100% -10px; }
  60% {
    background-position: 100% -5px; }
  /*0%, 100% {
    background-position: 100% 5px;
  }

  25% {
    background-position: 100% -5px;
  }*/ }

@keyframes wave {
  0%, 20%, 50%, 80%, 100% {
    background-position: 100% 0px; }
  40% {
    background-position: 100% -10px; }
  60% {
    background-position: 100% -5px; }
